
.formControl {
    display: inline-block;
    margin: 5px 10px 5px 0;
}

.errorText {
    color: red;
}

.formControl.error input {
    border: 2px solid red;
}

.input {
    display: inline-block;
}

.loginForm {
    color: #222222;
}

.loginForm button {
    margin-bottom: 20px;
}

.testData {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.testData :last-child :first-child {
    margin-bottom: 3px;
}
